.schedule-row {
  width: 100%;
  display: flex;
  flex-direction: column;
  font-family: 'Roboto', sans-serif;
  font-size: 24px;

  & .post {
    display: flex;
    border-bottom: 1px solid;
    margin-top: 40px;

    @media (max-width: 767px) {
      flex-direction: column;
    }

    & .day-title {
      flex-grow: 1;
      width: 360px;
      min-width: 360px;
      max-width: 360px;
      color: #c71120;
      padding-top: 6px;

      @media (max-width: 767px) {
        text-align: center;
      }
    }
    & .schedule-body {
      font-size: 20px;
      display: flex;
      flex-grow: 3;
      flex-direction: column;

      @media (max-width: 767px) {
        text-align: center;
      }

      & .even {
        background-color: #ffbf5542;
      }

      & .event {
        display: flex;
        margin: 10px 0;
        height: 100%;

        @media (max-width: 767px) {
          flex-direction: column;
        }

        & .left {
          flex-grow: 1;
          display: flex;
          width: 200px;
          max-width: 200px;
          min-width: 200px;
          align-items: center;

          @media (max-width: 767px) {
            justify-content: center;
            max-width: 100%;
            width: 100%;
          }

        }
        & .right {
          flex-grow: 4;

          & .desc {
            font-size: 16px;
          }
        }
      }
    }
  }

}

