.about {
    position: relative;
    width: 100%;
    height: auto;
    padding-bottom: 10px;
    margin: 0;

    .bg {
        position: absolute;
        background: url(../img/about/about_bg.png);
        z-index: -2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    .bg_2 {
        position: absolute;
        background: url(../img/about/about_bg_2.png) repeat-x;
        z-index: -1;
        top: 0;
        left: 0;
        width: calc(100% - 90px);
        height: calc(100% - 114px);
        margin: 57px 45px;
        box-sizing: border-box;
        opacity: 0.3;
    }

    .bg_3 {
        position: absolute;
        background-color: #c91123;
        z-index: -3;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    .container {
        padding-top: 70px;
        display: flex;
    }

    .title_block {
        .title {
            @include text-style (#FFF, 40px, 500, 1.2);
            font-family: Gilroy-Bold;
            text-transform: uppercase;
        }
    }

    .info_block {
        margin-bottom: 20px;

        .properties {
            display: flex;
            flex-wrap: wrap;

            &_item {
                width: 50%;
                margin-bottom: 40px;

                &_title {
                    @include text-style (#FFF, 32px, 700, 1.2);
                    font-family: Gilroy-Bold;
                }

                &_desc {
                    @include text-style (#FFF, 21px, 700, 1.2);
                    font-family: Gilroy;
                }
            }
        }

        .tagline {
            @include text-style (#FFF, 19px, 400, 1.2);
            font-family: Gilroy;
            text-transform: uppercase;
            width: 100%;
            max-width: 320px;
        }

        .smm_block {
            display: flex;
            justify-content: center;
            width: 100%;
            margin-right: 30px;
            margin-top: 10px;
            padding-top: 30px;

            a {
                display: inline-block;
                height: 32px;
                margin-right: 20px;
            }

            .fa {
                color: #000;
                font-size: 32px;
                transition: 0.3s all ease-out;
                cursor: pointer;
                position: relative;
                z-index: 10;
            }

            .fa:hover {
                color: #FFF
            }
        }
    }
}

@media only screen and (max-width: 768px) {
    .about {
        .bg_2 {
            background: none;
        }

        .container {
            flex-direction: column;
            align-items: center;
            justify-content: center;

            .title_block {
                margin-left: 0;
                width: 100%;
                text-align: center;
            }

            .info_block {
                width: 100%;

                .properties {
                    justify-content: center;
                }
            }

            .tagline {
                margin: 0;
            }

            .smm_block {
                justify-content: flex-end;
            }
        }
    }

}