section.program-committee {

  .main-title {
    font-family: 'Jura', sans-serif;
    font-size: 45px;
    color: $primary;
    margin-top: 15px;
  }

  .main_subtitle {
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    color: #000;
  }

  .program-committee-slider {

    .slick-next {
      right: -20px!important;
      z-index: 100;
      &::before {
        content: '\276F';
        color: #bcbcbc;
        font-size: 50px;
      }
    }

    .slick-prev {
      left: -20px!important;
      &::before {
        content: '\276E';
        color: #bcbcbc;
        font-size: 50px;
      }
    }

    .chairman-info {
      outline: none!important;
      padding: 10px 15px;

      .img-wrapper {
        cursor: pointer;
        width: 100%;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          filter: grayscale(100%);
        }
      }

      .insides-wrapper {
        @include flexbox;
        @include flex-direction(column);
        @include justify-content(space-between);
        @include align-items(center);
        border: 2px solid transparent;
        padding: 5px;
        &:hover {
          border: 2px solid red;
          border-radius: 9px;
        }
      }

      .chairman__name {
        font-family: 'Roboto', sans-serif;
        font-size: 25px;
        margin-top: 15px;
        font-weight: 600;
        text-align: center;
        cursor: pointer;
        min-height: 60px;
      }

      .chairman__position {
        font-family: 'Roboto', sans-serif;
        font-size: 16px;
        text-align: center;
        min-height: 60px;

      }

      .chairman__link-list {
        @include flexbox;
        @include justify-content(center);
        @include align-items(center);

        .chairman__link {
          margin-right: 20px;
          text-decoration: none;

          i {
            display: block;
            font-size: 20px;
            color: #8d8d8d;
            @include transition-all();
          }

          &:hover i{
            color: #e32d42;
          }

          &:last-child {
            margin-right:0;
          }
        }
      }
    }
  }
}

