// Global styling for this template
body,
html {
    width: 100%;
    height: 100%;
    font-family: Roboto, sans-serif;
}

body {
    @include sans-serif-font;
    padding-top: 63px;
}

@media screen and (max-width: 365.98px) {
    body {
        padding-top: 103px!important;
    }
}

hr {
    max-width: 50px;
    border-width: 3px;
    border-color: $primary;
}

hr.light {
    border-color: $white;
}

a {
    color: $primary;
    @include transition-all;
    &:hover {
        color: darken($primary, .1);
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    @include sans-serif-font;
}

.bg-primary {
    background-color: $primary !important;
}

.bg-dark {
    background-color: $gray-900 !important;
}

.text-faded {
    color: fade-out($white, .3);
}

section {
    padding: 3rem 0;
}

.section-heading {
    margin-top: 0;
}

::-moz-selection {
    color: $white;
    background: $gray-900;
    text-shadow: none;
}

::selection {
    color: $white;
    background: $gray-900;
    text-shadow: none;
}

img::selection {
    color: $white;
    background: transparent;
}

img::-moz-selection {
    color: $white;
    background: transparent;
}

body {
    -webkit-tap-highlight-color: $gray-900;
}

.block_title {
    padding-bottom: 1rem;
    @media (min-width: 992px) {
        padding-bottom: 2.7rem;
    }
}

section.paddings {
    margin-top: -1rem;
    padding-top: 4rem;
    padding-bottom: 3rem;
    @media (min-width: 992px) {
        margin-top: -4rem;
        padding-top: 6rem;
        padding-bottom: 3rem;
    }

}

.fixed-social {
    padding: 0!important;
    position: fixed;
    left: 20px;
    bottom: 20px;
    z-index: 2;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    @media (max-width: 768px) {
        flex-direction: column;
    }

    a {
        display: block;
        background: #fff;
        padding: 10px;
        border-radius: 50%;
        height: 42px;
        width: 42px;
        text-decoration: none;
        border: 1px solid #c7121f;
        text-align: center;
        margin-right: 10px;
        transition: all 0.2s linear;

        @media (max-width: 768px) {
            margin-bottom: 10px;
        }

        svg {
            font-size: 20px;
            color: #c7121f;
            transition: all 0.2s linear;
        }

        &:hover {
            background: #c7121f;

            svg {
                color: #fff;
            }
        }
    }
}

.btn {}

.new_btn{
    @include text-style (#fff, 20px, 400, 1.43);
    padding: 12px 17px;
    font-family: Gilroy-Bold;
    text-transform: uppercase;
    background-color: $primary;
    border: 1px $primary solid;
    border-radius: 8px;
    cursor: pointer;
    transition: 0.3s all ease-out;
    min-width: 200px;
    text-align: center;
    margin: 0 10px 25px 10px;
    &:hover{
        text-decoration: none;
        background-color: #fff;
        color: $primary;
    }
}

.img-responsive {
    width: 100%;
    height: auto;
    display: block;
}

.img-logo {
    width: 70px;
    height: auto;
}
.margin-top-6-rem {
    margin-top: 6rem;
}


/* Image lazy load*/
img {
    opacity: 1;
    transition: opacity 0.3s;
}

img[data-src] {
    opacity: 0;
}
/* Image lazy load*/
u {
    text-decoration: none;
}

.flex {
    display: flex;
}

.desktop_show {
    display: block;
}

.mobile_show {
    display: none;
}

.loading-bg {
    // background-image: url(../images/ninja-bg-gray.png);
    background-size: cover;
    background-position: center;
}

@media screen and (max-width: 767px) {

    .desktop_show {
        display: none;
    }

    .mobile_show {
        display: block;
    }



}
@media screen and (max-width: 600px) {
    .img-logo {
        width: 50px;
        height: auto;
    }
}
