@font-face {
    font-family: 'Jura';
    src: url('../fonts/JuraDemiBold.eot');
    src: url('../fonts/JuraDemiBold.eot?#iefix') format('embedded-opentype'),
        url('../fonts/JuraDemiBold.woff') format('woff'),
        url('../fonts/JuraDemiBold.ttf') format('truetype');
    font-weight: 600;
    font-style: normal;
}

@font-face {
    font-family: 'Jura';
    src: url('../fonts/JuraLight.eot');
    src: url('../fonts/JuraLight.eot?#iefix') format('embedded-opentype'),
        url('../fonts/JuraLight.woff') format('woff'),
        url('../fonts/JuraLight.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
}

@font-face {
    font-family: Gilroy;
    src: url('../fonts/GilroyLight.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: Gilroy-Bold;
    src: url('../fonts/GilroyExtraBold.otf') format('opentype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Roboto';
    src: url('../fonts/Roboto-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}