      .beacon{
        display: none;
      }
      .light{
        display: none;
      }
      .gull{
        width: 96px !important;
        height: 92px !important;
        right: 15px !important;
        img{
          width: 100%;
          height: 100%;
        }
      }
      .text-block{
        width: 80%;
        img{
          width: 100%;
        }
      }

    }
    .slide-4{

      .star_1{

        img{
          width: 92px;
          height: 83.2px;
        }
      }
      .star_2{
        width: 128px !important;
        height: 104px !important;
        img{
          width: 128px;
          height: 104px;
        }

        right: 0px !important;
      }
      .text-block{
        width: 80%;
        img{
          width: 100%;
        }
      }

    }
  }
}
